@import "../../assets/scss/app";

.experience {
  @include container;
  padding-top: 60px;

  @include desktop {
    padding-top: 160px;
  }

  .inner {
    @include inner;

    @include desktop {
      width: 954px;
    }

    .title {
      @include title;
      text-align: center;
    }

    .items {
      margin-top: 20px;

      @include desktop {
        margin-top: 94px;
        display: flex;
        justify-content: space-between;
      }

      .item {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include desktop {
          width: 407px;
        }

        &:last-child {
          margin-top: 40px;

          @include desktop {
            margin-top: 0;
          }
        }

        .img {
          display: block;
          width: 118px;
          height: 118px;

          @include desktop {
            width: 190px;
            height: 190px;
          }
        }

        .title {
          margin-top: 16px;
          font-weight: 600;
          font-size: 15px;
          line-height: 130%;
          text-align: center;
          letter-spacing: 0.04em;

          @include desktop {
            margin-top: 40px;
            font-size: 24px;
          }
        }

        .text {
          margin-top: 10px;
          font-weight: 300;
          font-size: 15px;
          line-height: 140%;
          text-align: center;

          @include desktop {
            margin-top: 24px;
            font-size: 18px;
          }
        }

        .btn {
          margin-top: 20px;
          width: 270px;
          margin-left: auto;
          margin-right: auto;

          @include desktop {
            margin-top: 40px;
            width: 240px;
          }
        }

      }


    }
  }
}